import { Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { ActivatedRoute, Params } from '@angular/router';

import { SpecialistsService } from '../../../+specialists/specialists.service';
import { Specialist } from '../../../+specialists/specialist';

import { PaymentsReportsService } from '../reports.service';
import { Payment } from '../../payment';

import { Person } from '../../../+persons/person';
import { Analyzes } from '../../../+analyzes/analyzes';

@Component({
  selector: 'app-payments-report-specialist-income-range',
  templateUrl: './report-specialist-income-range.component.html',
  styleUrls: ['./report-specialist-income-range.component.css'],
  providers: [PaymentsReportsService]
})
export class PaymentsReportSpecialistIncomeRangeComponent implements OnInit {
  report$: Observable<any>;
  reports: Object;
  specialist$: Observable<Specialist>;
  date_to: Date;
  date_from: Date;
  dateRange: Promise<Number>;
  specId: string;
  showZero: Boolean;
  specMode: string = 'specialists';

  constructor(
    private _prService: PaymentsReportsService,
    private _specialistsService: SpecialistsService,
    private _route: ActivatedRoute
  ) {}

  ngOnInit() {
    this._route.params.forEach((item: Params) => {
      if(item['specId']) {
        this.specId = item["specId"];
        this.getSpecialist(this.specId);
      }
    });
    this.initLocalData();

    this.initLocalDate()
      .then(range => this.reloadPayments(range));
  }

  reloadPayments(options?) {
      if(options) {
        this.date_to = options.date_to || this.date_to;
        this.date_from = options.date_from || this.date_from;
        this.specMode = options.mode || this.specMode;
      }
      this.report$ = this._prService.getDaysReport({date_from: this.date_from, date_to: this.date_to, specId: this.specId, mode: this.specMode});
      this.report$.subscribe(result => {
        this.reports = result;
        this.dateRange = this.createDateRange(this.date_from, this.date_to);
      });
  }

  getSpecialist(_id) {
    this.specialist$ = this._specialistsService.getOne(_id)
      .map((spec) => new Specialist(spec));
  }

  addDateRange() {
    localStorage.setItem('incomeDateRange', JSON.stringify({ date_to: this.date_to, date_from: this.date_from }));
    this.reloadPayments();
  }

  createDateRange(date_from, date_to) {
    return new Promise((resolve, reject) => {
      let start = new Date(date_from);
      start.setHours(0, 0, 0, 0);
      let end = new Date(date_to);
      end.setHours(0, 0, 0, 0);
      let result = [];
      while(start <= end) {
          result.push(start.getTime());
          start.setDate(start.getDate() + 1);
      }

      resolve(result);
    });
  }

  initLocalDate() {
    return new Promise((resolve, reject) => {
      let local = JSON.parse(localStorage.getItem('incomeDateRange'));
      if(local) resolve(local);
      else reject();
    });
    
  }

  toggleShowZero() {
    this.showZero = !this.showZero;
    localStorage.setItem('reportShowZero', JSON.stringify(this.showZero));
  }

  initLocalData() {
     this.showZero = !!JSON.parse(localStorage.getItem('reportShowZero'));
     this.specMode = localStorage.getItem('reportSpecMode') || 'specialists';
  }

}



