import { Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Observable } from 'rxjs/Observable';

import { SpecialistsService } from '../../../+specialists/specialists.service';
import { Specialist } from '../../../+specialists/specialist';
import { Person } from '../../../+persons/person';

import { PaymentsReportsService } from '../reports.service';

@Component({
  selector: 'app-payments-report-person-list',
  templateUrl: './report-person-list.component.html',
  styleUrls: ['./report-person-list.component.css'],
  providers: [PaymentsReportsService]
})
export class PaymentsReportPersonListComponent implements OnInit {
  report$: Observable<any>;
  specialist$: Observable<Specialist>;

  date_to: Date;
  date_from: Date;
  specId: string;

  constructor(
    private _prService: PaymentsReportsService,
    private _specialistsService: SpecialistsService,
    private _route: ActivatedRoute
  ) {}

  ngOnInit() {
    this._route.params.forEach((item: Params) => {
      if(item['specId']) {
        this.specId = item["specId"];
        this.getSpecialist(this.specId);
      }
    });

    this.initLocalDate()
      .then(range => {
        this.reloadReport(range)
      });
  }

  getSpecialist(_id) {
    this.specialist$ = this._specialistsService.getOne(_id)
      .map((spec) => new Specialist(spec));
  }

  reloadReport(options?) {
      if(options) {
        this.date_to = options.date_to || this.date_to;
        this.date_from = options.date_from || this.date_from;
      }

      this.report$ = this._prService.getPersonListReport({date_from: this.date_from, date_to: this.date_to, specId: this.specId})
        .map((result) => result.map((item) => {
            item.person = new Person(item.person);
            return item;
        }));

    this.report$.subscribe((result) => console.log(result));
  }

  initLocalDate() {
    return new Promise((resolve, reject) => {
      let local = JSON.parse(localStorage.getItem('incomeDateRange'));
      if(local) resolve(local);
      else reject();
    });
  }

}



