import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Observable } from 'rxjs/Observable';

import { Payment } from '../payment';
import { PaymentsService } from '../payments.service';
import { PaymentFormComponent } from '../payment-form/payment-form.component';

import { SpecialistsService } from '../../+specialists/specialists.service';
import { Specialist } from '../../+specialists/specialist';

import { ToolsService } from '../../shared/tools.service';

@Component({
  selector: 'app-payment-pay',
  templateUrl: './payment-pay.component.html',
  styleUrls: ['./payment-pay.component.css']
})
export class PaymentPayComponent implements OnInit {
  @ViewChild(PaymentFormComponent) paymentForm: PaymentFormComponent;
  specialists$: Observable<Specialist[]>;

  paymentFormOptions = {
    persons: false,
    specialists: false,
    services: true,
    payment: true
  };

  form: FormGroup;
  submited: boolean = false;
  payment: Payment = new Payment();

  constructor(
    private specialistsService: SpecialistsService,
    private paymentService: PaymentsService,
    private fb: FormBuilder,
    private tools: ToolsService,
    private _route: ActivatedRoute,
    private _router: Router
  ) { }

  ngOnInit() {
    this.form = this.getFormGroup();
    this.reloadSpecialists();

    this._route.params.forEach((item: Params) => {
      if(item['id']) {
        this.loadPayment(item['id']);
      }
    });
  }

  loadPayment(_id: string) {
    let payment$ = this.paymentService.getOne(_id);

    payment$.subscribe((item) => {
      this.payment = new Payment(item);
      this.payment.specialists.forEach((id) => this.getSpecialist(id));
      this.payment.assistant.forEach((id) => this.getAssistant(id));
      this.form.patchValue(this.payment);
    });
  }

  reloadSpecialists() {
    this.specialists$ = this.specialistsService.get();
  }

  getSpecialist(id) {
    // let specialist$ = this.specialistsService.getOne(id);

    // specialist$.subscribe((spec) => {
    //   this.paymentForm.checkSpecialists.push(new Specialist(spec));
    // });
  }

  getAssistant(id) {
    let specialist$ = this.specialistsService.getOne(id);

     specialist$.subscribe((spec) => {
      this.paymentForm.checkAssistants.push(new Specialist(spec));
    });
  }

  sendData(payment: Payment) {
    if(!this.submited) {
      this.submited = true;
      payment.status = "closed";
      let paymentPay$ = this.paymentService.pay(payment);

      delete payment.serialNumber;

      paymentPay$.subscribe(
        (result) => {
          //this.tools.getToast().toast('Оплата внесена успешно', 1000, 'color: green');
          this.toList();
        },
        (errors: any[]) => {
          this.submited = false;
          errors.forEach((error) => this.tools.getToast().toast(error, 3000, 'color: red'))
        });
    }
  }

  getFormGroup() {
    return this.fb.group({
      _id: this.fb.control(''),
      personId: this.fb.control(''),
      specialists: this.fb.control(''),
      referral: this.fb.control(''),
      assistant: this.fb.control(''),
      payment: this.fb.group({
          type: this.fb.control('', Validators.required),
          paid: this.fb.control('', Validators.required),
          inKass: this.fb.control('')
      }), 
      type: this.fb.control('', Validators.required),
      discount: this.fb.group({
          services: this.fb.group({
              isPercent: this.fb.control(''),
              number: this.fb.control('')
          }),
          analyzes: this.fb.group({
              isPercent: this.fb.control(''),
              number: this.fb.control('')
          }),
          goods: this.fb.group({
              isPercent: this.fb.control(''),
              number: this.fb.control('')
          })
      }),
      status: this.fb.control(''),
      search: this.fb.control(''),
      note: this.fb.control(''),
      person: this.fb.group({
        first_name: this.fb.control(''),
        second_name: this.fb.control(''),
        last_name: this.fb.control(''),
        gender: this.fb.control(''),
        birthdate: this.fb.control(''),
        phone: this.fb.control(''),
        email: this.fb.control(''),
        picture: this.fb.control('')
      })
    });
  }

  toList() {
    this._router.navigate(['payments']);
  }

}



