import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Observable } from 'rxjs/Observable';

import { Payment } from '../payment';
import { PaymentsService } from '../payments.service';
import { PaymentFormServicesSelectComponent } from '../payment-form-services-select/payment-form-services-select.component';

import { SpecialistsService } from '../../+specialists/specialists.service';
import { Specialist } from '../../+specialists/specialist';

import { ToolsService } from '../../shared/tools.service';


@Component({
  selector: 'app-payment-create',
  templateUrl: './payment-create.component.html',
  styleUrls: ['./payment-create.component.css']
})
export class PaymentCreateComponent implements OnInit {
  @ViewChild(PaymentFormServicesSelectComponent) paymentFormServicesSelect: PaymentFormServicesSelectComponent;
  
  // specialists
  specialists$: Observable<Specialist[]>;
  specialistsChecked$: Observable<Specialist[]>;
  paymentAdd$: Observable<Payment>;

  paymentFormOptions: Observable<{
    persons?: boolean;
    specialists?: boolean;
    payment?: boolean;
  }>;

  form: FormGroup;
  submited: boolean = false;
  payment: Payment = new Payment();
  
  constructor(
    private specialistsService: SpecialistsService,
    private paymentService: PaymentsService,
    private fb: FormBuilder,
    private tools: ToolsService,
    private _route: ActivatedRoute,
    private _router: Router
  ) { 
    this.form = this.getFormGroup();
    this.logSpecialistsChange();
    this.loadSpecialistByAuth();
    this.changePaymentFormOptions({ persons: true , specialists: true});
  }

  ngOnInit() {
    this.reloadSpecialists();

    this._route.params.forEach((item: Params) => {
      if(item['id']) {
        this.loadPayment(item['id']);
      }
    });
  }

  loadPayment(_id: string) {
    let payment$ = this.paymentService.getOne(_id);

    payment$.subscribe((item) => {
      this.payment = new Payment(item);

      this.form.patchValue(this.payment);
      this.changePaymentFormOptions({ persons: true });
    });
  }

  changePaymentFormOptions(params: {
    persons?: boolean;
    specialists?: boolean;
    payment?: boolean;
  }) {
    this.paymentFormOptions = new Observable<any>( observer => observer.next({
      persons: !!params.persons,
      specialists: !!params.specialists,
      payment: !!params.payment
    }));
  }

  reloadSpecialists() {
    this.specialists$ = this.specialistsService.get();

    this.specialists$.subscribe();
  }

  loadSpecialistByAuth() {
    let specialistAuth$ = this.specialistsService.getOneByAuth();
    specialistAuth$.subscribe((spec) => {
      this.payment.specialists = [spec._id];
      this.form.patchValue(this.payment);
      this.changePaymentFormOptions({persons: true});
      this.checkSpecialists([spec._id]);
    });
  }

  checkSpecialists(_ids: string[]) {
    this.specialists$.forEach((v: Specialist[]) => {
      this.specialistsChecked$ = new Observable((observer) => {
        observer.next(v.filter((item: Specialist) => {
          if(_ids.indexOf(item._id) >= 0 ) return true;
        }));
      });

      this.specialistsChecked$.subscribe();
    });
  }

  sendData(payment: Payment) {
    payment.status = "collected";

    payment.services = this.paymentFormServicesSelect.getCheckedServices();
    payment.analyzes = this.paymentFormServicesSelect.getCheckedAnalyzes();
    payment.goods = this.paymentFormServicesSelect.getCheckedGoods();

    delete payment.serialNumber;
    
    if(payment.services.length === 0 && payment.analyzes.length === 0 && payment.goods.length === 0) {
      this.tools.getToast().toast("Вы не выбрали услугу, анализ или товар", 3000, "color: red");
    } else if(!this.submited) {
      this.submited = true;
      let paymentAdd$ = this.paymentService.add(payment);

      if(!!payment._id) {
        paymentAdd$ = this.paymentService.collect(payment);
      }

      paymentAdd$.subscribe(
        (result) => {
          //this.tools.getToast().toast('Талон на оплату создан', 1000, 'color: green');
          this.toList();
        },
        (errors: any[]) => {
          this.submited = false;
          errors.forEach((error) => this.tools.getToast().toast(error, 3000, 'color: red'))
        });
    }
  }

  getFormGroup() {
    return this.fb.group({
      _id: this.fb.control(''),
      personId: this.fb.control(''),
      specialists: this.fb.control('', Validators.required),
      referral: this.fb.control(''),
      assistant: this.fb.control(''),
      services: this.fb.control(''),
      analyzes: this.fb.control(''),
      payment: this.fb.group({
          type: this.fb.control(''),
          paid: this.fb.control(''),
          inKass: this.fb.control('')
      }), 
      type: this.fb.control(''),
      discount: this.fb.group({
          services: this.fb.group({
              isPercent: this.fb.control(''),
              number: this.fb.control('')
          }),
          analyzes: this.fb.group({
              isPercent: this.fb.control(''),
              number: this.fb.control('')
          }),
          goods: this.fb.group({
              isPercent: this.fb.control(''),
              number: this.fb.control('')
          })
      }),
      status: this.fb.control(''),
      search: this.fb.control(''),
      note: this.fb.control(''),
      person: this.fb.group({
        _id: this.fb.control(''),
        first_name: this.fb.control('', Validators.required),
        second_name: this.fb.control(''),
        last_name: this.fb.control('', Validators.required),
        gender: this.fb.control(''),
        birthdate: this.fb.control(''),
        phone: this.fb.control('', Validators.required),
        email: this.fb.control(''),
        picture: this.fb.control('')
      })
    });
  }

  private logSpecialistsChange() {
    const specialistsControl = this.form.get('specialists');

    specialistsControl.valueChanges.forEach(
      (value: string[]) => this.checkSpecialists(value)
    );
  }

  toList() {
    this._router.navigate(['payments']);
  }
}



