import { Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Observable } from 'rxjs/Observable';

import { SpecialistsService } from '../../../+specialists/specialists.service';
import { Specialist } from '../../../+specialists/specialist';

import { PaymentsReportsService } from '../reports.service';
import { rAnalyzes } from '../rAnalyzes';

@Component({
  selector: 'app-payments-report-performed-work',
  templateUrl: './report-performed-work.component.html',
  styleUrls: ['./report-performed-work.component.css'],
  providers: [PaymentsReportsService]
})
export class PaymentsReportPerformedWorkComponent implements OnInit {
  report$: Observable<any>;
  reports: Object;
  specialist$: Observable<Specialist>;

  date_to: Date;
  date_from: Date;
  specId: string;

  constructor(
    private _prService: PaymentsReportsService,
    private _specialistsService: SpecialistsService,
    private _route: ActivatedRoute
  ) {}

  ngOnInit() {
    this._route.params.forEach((item: Params) => {
      if(item['specId']) {
        this.specId = item["specId"];
        this.getSpecialist(this.specId);
      }
    });

    this.initLocalDate()
      .then(range => {
        console.log('after init');
        this.reloadReport(range)
      });
  }

  getSpecialist(_id) {
    this.specialist$ = this._specialistsService.getOne(_id)
      .map((spec) => new Specialist(spec));
  }

  reloadReport(options?) {
      if(options) {
        this.date_to = options.date_to || this.date_to;
        this.date_from = options.date_from || this.date_from;
      }
      this.report$ = this._prService.getPerfomedWorkReport({date_from: this.date_from, date_to: this.date_to, specId: this.specId})
        .map((report) => {
          report.analyzes = report.analyzes.map(analyz => {
            analyz.info = new rAnalyzes(analyz.info);
            return analyz;
          });

          return report;
        });
      this.report$.subscribe(result => {
        this.reports = result;
      });
  }

  initLocalDate() {
    return new Promise((resolve, reject) => {
      let local = JSON.parse(localStorage.getItem('incomeDateRange'));
      if(local) resolve(local);
      else reject();
    });
  }

}



