import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import 'rxjs/Rx';
import { Observable } from 'rxjs/Rx';
import { HttpClient } from '../../+users/_services/http-client.service';

@Injectable()
export class PaymentsReportsService {
  private _baseUrl: string = "/api/v1/payments/reports";

  constructor(
    private _http: HttpClient
  ) {}

	getDayReport(params?: any) : Observable<any> {
		let query = this.createQuaryParams(params);

		return this._http.get(this._baseUrl + '/day', { search: query })
			.map( (res: Response) => res.json())
			.catch( (error: any) => Observable.throw(error || "Server error"))
			.share();
	}

  getIncomeMonthReport(params?: any) : Observable<any> {
    let query = this.createQuaryParams(params);
    
		return this._http.get(this._baseUrl + '/incomedate', { search: query })
			.map( (res: Response) => res.json())
			.catch( (error: any) => Observable.throw(error || "Server error"))
      .share();
  }

  getDaysReport(params?: any) : Observable<any> {
    let query = this.createQuaryParams(params);
    
		return this._http.get(this._baseUrl + '/incomedatespec', { search: query })
			.map( (res: Response) => res.json())
			.catch( (error: any) => Observable.throw(error || "Server error"))
      .share();
  }

  getPerfomedWorkReport(params?: any) : Observable<any> {
    let query = this.createQuaryParams(params);
    
		return this._http.get(this._baseUrl + '/performedwork', { search: query })
			.map( (res: Response) => res.json())
			.catch( (error: any) => Observable.throw(error || "Server error"))
      .share();
  }

  getPersonListReport(params?: any) : Observable<any> {
    let query = this.createQuaryParams(params);
    
		return this._http.get(this._baseUrl + '/persons', { search: query })
			.map( (res: Response) => res.json())
			.catch( (error: any) => Observable.throw(error || "Server error"))
      .share();
  }

  private createQuaryParams(params) : URLSearchParams {
      let query = new URLSearchParams();

      if(params) {
          if(params.date_from) query.set('date_from', params.date_from);
          if(params.date_to) query.set('date_to', params.date_to);
          if(params.specId) query.set('specId', params.specId);
          if(params.mode) query.set('mode', params.mode);
      }

      return query;
  }
}



