import { Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Observable } from 'rxjs/Observable';

import { PaymentsReportsService } from '../reports.service';
import { Payment } from '../../payment';

import { Person } from '../../../+persons/person';
import { Analyzes } from '../../../+analyzes/analyzes';
import { rAnalyzes } from '../rAnalyzes';

@Component({
  selector: 'app-payments-report-full',
  templateUrl: './report-full.component.html',
  styleUrls: ['./report-full.component.css'],
  providers: [PaymentsReportsService]
})
export class PaymentsReportFullComponent implements OnInit {
  report$: Observable<any>;
  current: any;
  currentDate: Date;
  
  fullServicesPrice: Number;
  fullAnalyzesPrice: Number;
  fullGoodsPrice: Number;
  fullDiscountsPrice: Number;
  fullPaymentPrice: Number;
  fullDebt: Number;

  specId: string;
  specMode: string = 'specialists';

  constructor(
    private _prService: PaymentsReportsService,
    private _route: ActivatedRoute
  ) {}

  ngOnInit() {
    this.initDay();

    this._route.params.forEach((item: Params) => {
      if(item['specId'] && item['date']) {
        this.specId = item["specId"];
        this.currentDate = new Date(parseInt(item['date']));
        this.initLocalData();
        this._reloadPaymentByDate();
      } else {
        this.reloadReport();
      }
    });
  }

  initDay() {
    let today = new Date();
    today.setHours(0, 0, 0, 0);

    this.currentDate = today;
  }

  nextDay() {
    let next = new Date(this.currentDate.getTime() + (24 * 60 * 60 * 1000));
    this.currentDate = next;
    this._reloadPaymentByDate();
  }

  prevDay() {
    let prev = new Date(this.currentDate.getTime() - (24 * 60 * 60 * 1000));
    this.currentDate = prev;
    this._reloadPaymentByDate();
  }

  _reloadPaymentByDate() {
    let next = new Date(this.currentDate.getTime() + (24 * 60 * 60 * 1000));
    let params = { date_from: this.currentDate, date_to: next };
    if(this.specId) params["specId"] = this.specId;
    params['mode'] = this.specMode;

    this.reloadReport(params);
  }

  reloadReport(options?) {
    
    this.report$ = this._prService.getDayReport(options)
      .map(paymentsInfo => {
        this.fullServicesPrice = paymentsInfo.fullServicesPrice;
        this.fullAnalyzesPrice = paymentsInfo.fullAnalyzesPrice;
        this.fullGoodsPrice = paymentsInfo.fullGoodsPrice;
        this.fullDiscountsPrice = paymentsInfo.fullDiscountsPrice;
        this.fullPaymentPrice = paymentsInfo.fullPaymentPrice;
        this.fullDebt = paymentsInfo.fullDebt;
        
        return paymentsInfo;
      })
      .map(paymentsInfo => paymentsInfo.items.map((pay) => {
        if(pay._id) {
          pay.specialists.person = new Person(pay.specialists.person);
        }

        if(!!pay.analyzesInfo) {
          pay.analyzesInfo.analyzes.forEach((analyz, index) => pay.analyzesInfo.analyzes[index] = new rAnalyzes(analyz));
        }
        return pay;
      }));
  }

  checkCurrent(item) {
    this.current = item;
  }

  initLocalData() {
     this.specMode = localStorage.getItem('reportSpecMode') || 'specialists';
  }

}



