import { Person, IPerson } from '../+persons/person';
import { Service } from '../+services/service';
import { Analyzes } from '../+analyzes/analyzes';
import { Good } from '../+goods/good';
import { IDiscount, Discount } from './discount';

export interface IPayment {
    _id?: string;
    serialNumber: Number;
    personId: string;
    specialists: any[];
    referral: string;
    assistant: any[];
    services: Service[];
    analyzes: Analyzes[];
    goods: Good[];
    payment: {
        type: string;
        paid: number;
        inKass: boolean;
    };
    type: string;
    discount: IDiscount;
    status: string;
    note: string;
}

export class Payment implements IPayment{
    _id?: string;
    serialNumber: Number;
    personId: string;
    specialists: any[];
    referral: string;
    assistant: any[];
    services: Service[];
    analyzes: Analyzes[];
    goods: Good[];
    payment: {
        type: string;
        paid: number;
        inKass: boolean;
    };
    type: string;
    discount: Discount;
    status: string;
    person: Person;
    note: string;
    createdAt: Date;

    get toPay() {
        let summService = 0;
        let summAnalyzes = 0;
        let summGoods = 0;

        this.services.forEach((item) => summService += item.price);
        this.analyzes.forEach((item) => summAnalyzes += item.price);
        this.goods.forEach((item) => summGoods += item.price);

        let summ = this.discount.calculate(summService, 'services') + this.discount.calculate(summAnalyzes, 'analyzes') + this.discount.calculate(summGoods, 'goods');
        return summ.toFixed(2);
    }

    constructor(options?) {
        this.default();
        if(options) {
            this.setParams(options);
        }
    }

    setParams(options: {
        _id?: string,
        serialNumber?: number,
        personId?: string,
        specialists?: any[],
        referral?: string,
        assistant?: any[],
        services?: Service[],
        analyzes?: Analyzes[],
        goods?: Good[],
        payment?: {
            type: string,
            paid: number,
            inKass: boolean,
        },
        type?: string,
        discount?: IDiscount,
        status?: string,
        person?: Person,
        note?: string,
        createdAt?: Date
    }) {
        this._id = options._id || null;
        this.serialNumber = options.serialNumber || 1;
        this.personId = options.personId || null;
        this.specialists = options.specialists || [];
        this.referral = options.referral || null;
        this.assistant = options.assistant || [];
        if(options.services) {
            this.services = options.services.map((item) => new Service(item));
        }
        if(options.analyzes) {
            this.analyzes = options.analyzes.map((item) => new Analyzes(item));
        }
        if(options.goods) {
            this.goods = options.goods.map((item) => new Good(item));
        }
        this.payment = options.payment || {
            type: 'cash',
            paid: 0,
            inKass: false   
        };
    
        this.type = options.type || 'default';
        this.discount = new Discount(options.discount);
           
        this.status = options.status || 'open';
        if(options.person) {
            this.person.setParams(options.person);
        }
        this.note = options.note || null;
        this.createdAt = options.createdAt ? new Date(options.createdAt) : null; 
    }

    default() {
        this._id = null;
        this.serialNumber = null;
        this.personId = null;
        this.specialists = [];
        this.referral = null;
        this.assistant = [];
        this.services = [];
        this.analyzes = [];
        this.goods = [];
        this.payment = {
            type: 'cash',
            paid: null,
            inKass: false   
        };
    
        this.type = 'default';
        this.discount = new Discount();          
        this.status = 'open';
        this.person = new Person();
        this.note = null;
        this.createdAt = null;
    }

    isCollect() {
        this.status = 'collected';
    }

    isClose() {
        this.status = 'closed';
    }

    isOpen() {
        this.status = 'open';
    }

    getSpecialists() {
        if(this.specialists.length > 0 && this.specialists[0] !== null) {
            return this.specialists.map((item) => [item.person.last_name, item.person.first_name[0] + '.', item.person.second_name[0] + '.'].join(" "));
        }
        return [];
    }
}


