export interface IDiscount {
    services: {
        isPercent: boolean;
        number: number;
    };
    analyzes: {
        isPercent: boolean;
        number: number;
    };
    goods: {
        isPercent: boolean;
        number: number;
    };
}

export interface IDiscountService {

}

export class Discount implements IDiscountService {
    services: {
        isPercent: boolean;
        number: number;
    };
    analyzes: {
        isPercent: boolean;
        number: number;
    };
    goods: {
        isPercent: boolean;
        number: number;
    };

    constructor(options?) {
        this.default();
        if(options) {
            this.setParams(options);
        }
    }

    setParams(options: {
      services?: {
        isPercent: boolean;
        number: number;
      },
      analyzes?: {
        isPercent: boolean;
        number: number;
      },
      goods?: {
        isPercent: boolean;
        number: number;
      }
    }) {
        this.services = options.services || { number: null, isPercent: false };
        this.analyzes = options.analyzes || { number: null, isPercent: false };
        this.goods = options.goods || { number: null, isPercent: false };
    }

    default() {
        this.services = { number: null, isPercent: false };
        this.analyzes = { number: null, isPercent: false };
        this.goods = { number: null, isPercent: false };
    }

    calculate(number, type) {
        var result = 0;
        switch(type) {
            case 'services':
                result = number - this.getDiscount(number, 'services');
                break;
            case 'analyzes':
                result = number - this.getDiscount(number, 'analyzes');
                break;
            case 'goods':
                result = number - this.getDiscount(number, 'goods');
                break;
            default:
                result = number;
                break;
        }

        return result;
    }

    private getDiscount(number, type) {
        return this[type].isPercent ? (number * (parseInt(this[type].number || 0) / 100)) : parseInt(this[type].number || 0);
    }
}



