import { Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { Observable } from 'rxjs/Observable';

import { PaymentsReportsService } from '../reports.service';
import { Payment } from '../../payment';

import { Person } from '../../../+persons/person';
import { Analyzes } from '../../../+analyzes/analyzes';

@Component({
  selector: 'app-payments-report-mini',
  templateUrl: './report-mini.component.html',
  styleUrls: ['./report-mini.component.css'],
  providers: [PaymentsReportsService]
})
export class PaymentsReportMiniComponent implements OnInit {
  report$: Observable<any>;
  current: any;
  currentDate: Date;
  
  fullServicesPrice: Number;
  fullAnalyzesPrice: Number;
  fullGoodsPrice: Number;
  fullDiscountsPrice: Number;
  fullPaymentPrice: Number;
  fullDebt: Number;

  constructor(
    private _prService: PaymentsReportsService
  ) {}

  ngOnInit() {
    this.reloadReport();
    this.initDay();
  }

  initDay() {
    let today = new Date();
    today.setHours(0, 0, 0, 0);

    this.currentDate = today;
  }

  nextDay() {
    let next = new Date(this.currentDate.getTime() + (24 * 60 * 60 * 1000));
    this.currentDate = next;
    this._reloadPaymentByDate();
  }

  prevDay() {
    let prev = new Date(this.currentDate.getTime() - (24 * 60 * 60 * 1000));
    this.currentDate = prev;
    this._reloadPaymentByDate();
  }

  _reloadPaymentByDate() {
    let next = new Date(this.currentDate.getTime() + (24 * 60 * 60 * 1000));
    this.reloadReport({ date_from: this.currentDate, date_to: next });
  }

  reloadReport(options?) {
    this.report$ = this._prService.getDayReport(options)
      .map(paymentsInfo => {
        this.fullServicesPrice = paymentsInfo.fullServicesPrice;
        this.fullAnalyzesPrice = paymentsInfo.fullAnalyzesPrice;
        this.fullGoodsPrice = paymentsInfo.fullGoodsPrice;
        this.fullDiscountsPrice = paymentsInfo.fullDiscountsPrice;
        this.fullPaymentPrice = paymentsInfo.fullPaymentPrice;
        this.fullDebt = paymentsInfo.fullDebt;
        
        return paymentsInfo;
      })
      .map(paymentsInfo => paymentsInfo.items.map((pay) => {
        if(pay._id) {
          pay.specialists.person = new Person(pay.specialists.person);
        }

        if(!!pay.analyzesInfo) {
          pay.analyzesInfo.analyzes.forEach((analyz, index) => pay.analyzesInfo.analyzes[index] = new Analyzes(analyz));
        }
        return pay;
      }));
  }

  checkCurrent(item) {
    this.current = item;
  }

}



