import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, FormArray, Validators } from '@angular/forms';
import { Observable } from 'rxjs/Observable';
import { CustomValidators } from '../../shared/forms/custom-validators';

import { PersonFormContentComponent } from '../../+persons/_widgets/person-form-content/person-form-content.component';
import { PersonsService } from '../../+persons/persons.service';
import { Person } from '../../+persons/person'; 

import { Payment, IPayment } from '../payment';
import { Discount } from '../discount';

@Component({
  selector: 'app-payment-form',
  templateUrl: './payment-form.component.html',
  styleUrls: ['./payment-form.component.css']
})
export class PaymentFormComponent implements OnInit {
  @Input('formValues') payment: Payment = new Payment();
  @Input('services') services: any[] = [];
  //@Input('specialists') specialists: any[] = [];
  @Input('specialists') set specialists(items) {
      this._specialists = items;
      this.checkSpecialists = this.searchSpecialists(this.payment.specialists);
      this.checkAssistants = this.searchSpecialists(this.payment.assistant);
    };
  @Input('options') 
    set options(options: {
      persons: boolean;
      specialists: boolean;
      assistants: boolean;
      services: boolean;
      payment: boolean;
    }) {
      if(options) {
        this._optionsView.persons = !!options.persons;
        this._optionsView.specialists = !!options.specialists;
        this._optionsView.assistants = !!options.assistants;
        this._optionsView.services = !!options.services;
        this._optionsView.payment = !!options.payment;
      }
    }
  @Input('form') form: FormGroup = this.createFormGroupFull();

  @ViewChild(PersonFormContentComponent) personForm: PersonFormContentComponent;

  @Output('submitForm') 
    eSubmit: EventEmitter<IPayment> = new EventEmitter<IPayment>();
  @Output('searchPerson')
    eSearchPerson: EventEmitter<string> = new EventEmitter<string>();
  @Output('close')
    eClose: EventEmitter<string> = new EventEmitter<string>();

  foundPersons: Person[];
  _optionsView = {
    persons: true,
    specialists: true,
    assistants: true,
    services: false,
    payment: true
  };
  _specialists: any[] = [];
  checkSpecialists: any[] = [];
  checkAssistants: any[] = [];

  get specialists() {
    return this._specialists;
  }
  get options() {
    return this._optionsView;
  }

  constructor(
    private fb: FormBuilder,
    private pServices: PersonsService
  ) { }

  ngOnInit() {
    this.payment.isOpen();
    this.form.patchValue(this.payment);

    this.form.get('discount').valueChanges.subscribe( (discount) => {
      this.payment.discount = new Discount(discount);
    });
  }

  onSubmit() {
    let data = this.createDataForSave();
    
    this.eSubmit.emit(data);
  }

  onSearch(target) {
    this.reloadPersons(this.form.controls['search'].value);
  }

  onCloseSearchPerson() {
    this.form.patchValue({
      search: null,
      personId: null
    });
    this.personForm.form.enable();
  }

  selectPerson(person: Person) {
    this.payment.setParams(this.form.value);
    this.payment.person = person;

    this.form.patchValue(this.payment);
    this.form.patchValue({personId: person._id});
    this.personForm.disable();
  }

  private createFormGroupFull() {
    return this.fb.group({
      _id: this.fb.control(''),
      personId: this.fb.control(''),
      specialists: this.fb.control(''),
      referral: this.fb.control(''),
      assistant: this.fb.control(''),
      services: this.fb.control(''),
      analyzes: this.fb.control(''),
      payment: this.fb.group({
          type: this.fb.control(''),
          paid: this.fb.control(''),
          inKass: this.fb.control('')
      }), 
      type: this.fb.control(''),
      discount: this.fb.group({
          services: this.fb.group({
              isPercent: this.fb.control(''),
              number: this.fb.control('')
          }),
          analyzes: this.fb.group({
              isPercent: this.fb.control(''),
              number: this.fb.control('')
          }),
          goods: this.fb.group({
              isPercent: this.fb.control(''),
              number: this.fb.control('')
          })
      }),
      status: this.fb.control(''),
      search: this.fb.control(''),
      note: this.fb.control(''),
      person: this.fb.group({
        _id: this.fb.control(''),
        first_name: this.fb.control(''),
        second_name: this.fb.control(''),
        last_name: this.fb.control(''),
        gender: this.fb.control(''),
        birthdate: this.fb.control(''),
        phone: this.fb.control(''),
        email: this.fb.control(''),
        picture: this.fb.control('')
      })
    });
  }

  private reloadPersons(search?: string) {
    let person$ = this.pServices.get({text: search});

    person$.subscribe((persons) => {
      this.foundPersons = persons.items.map((item) => {
        return new Person(item);
      });
    });
  }

  searchSpecialists(items) {
    let specialists = [];
    items.forEach((id) => {
      let found = null;
      if(found = this.getSpecialistById(id)) specialists.push(found);
    });
    return specialists;
  }

  getSpecialistById(id) {
      return this.specialists.find((spec) => {
        if(spec._id == id) return true;
      }) || false;
  }

  onClose() {
    this.eClose.emit();
  }

  private createDataForSave() {
    var payment = this.form.value;

    if(this.form.controls['person'].dirty || this.form.controls['person'].valid) {
      payment.person = this.form.controls['person'].value;
    }

    if(payment.referral == "null") payment.referral = null;

    payment.personId = this.form.controls['person'].value._id || payment.personId;

    return payment;
  }

  getListServices() {
    var list = [];
    var added = {};

    this.payment.services.forEach((service) => {
      if(!!added[service._id]) {
        list.find((found, index) => {
          if(found.service._id == service._id) {
            list[index].count = list[index].count + 1;
            return true;
          }
        });
      }
      else {
        added[service._id] = true;
        list.push({count: 1, service: service});
      }
    });

    return list;
  }

  getListGoods() {
    var list = [];
    var added = {};

    this.payment.goods.forEach((good) => {
      if(!!added[good._id]) {
        list.find((found, index) => {
          if(found.good._id == good._id) {
            list[index].count = list[index].count + 1;
            return true;
          }
        });
      }
      else {
        added[good._id] = true;
        list.push({count: 1, good: good});
      }
    });

    return list;
  }

}



