import { Component, OnInit, ViewChild, Input, OnDestroy } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';

import { PaginationService } from '../../shared/pagination.service';
import { ToolsService } from '../../shared/tools.service';

import { GoodsListComponent } from '../../+goods/_widgets/goods-list/goods-list.component';
import { GoodsService } from '../../+goods/goods.service';
import { Good } from '../../+goods/good';

import { ServicesListComponent } from '../../+services/_widgets/services-list/services-list.component';
import { ServicesService } from '../../+services/services.service';
import { Service } from '../../+services/service';

import { AnalyzesProductListComponent } from '../../+analyzes/_widgets/analyzes-product-list/analyzes-product-list.component';
import { AnalyzesService } from '../../+analyzes/analyzes.service';
import { Analyzes } from '../../+analyzes/analyzes';

import { Specialist } from '../../+specialists/specialist';
import { Discount } from '../discount';
import { Payment } from '../payment';

@Component({
  selector: 'app-payment-form-services-select',
  templateUrl: './payment-form-services-select.component.html',
  styleUrls: ['./payment-form-services-select.component.css'],
  providers: [ServicesService, AnalyzesService]
})
export class PaymentFormServicesSelectComponent implements OnInit {
  @ViewChild(ServicesListComponent) servicesList: ServicesListComponent;
  @ViewChild(AnalyzesProductListComponent) analyzesList: AnalyzesProductListComponent;
  @ViewChild(GoodsListComponent) goodsList: GoodsListComponent;

  @Input('form') form: FormGroup;
  @Input('payment') set payment(payment: Payment) {
    this._payment = payment;
    this.setSelectedItems();
    this.reloadListChecked();
  }
  
  @Input('specialists') 
    set specialists(specialists: Specialist[]) {
      this._specialists = specialists;
      this.setServicesFilterBySpecialists(specialists);

      if(!!this.specialists) this.reloadServices(this.servicesFilter);
    }

  _specialists: Specialist[];
  _payment: Payment = new Payment();
  get payment() { return this._payment };

  get specialists() {
    return this._specialists;
  }

  servicesFilter: {
    variant: string,
    tags: string[],
    search: string
  } = {
    variant: 'default',
    tags: [],
    search: ''
  };

  profiles: any[];

  // Services
  services$: Observable<Service[]>;
  servicesSubscription: Subscription;
  servicesListOptions: {} = {
    tools: {
      add: true
    },
    price: true,
    tags: false
  };

  // Analyzes
  analyzes$: Observable<Analyzes[]>;
  analyzesSubscription: Subscription;
  analyzesListOptions = {
    tools: {
      check: true
    },
    view: {
      price: true
    }
  };

  // Analyzes
  goods$: Observable<Good[]>;
  goodsSubscription: Subscription;
  goodsListOptions = {
    tools: {
      add: true
    },
    price: true
  };

  checkedServices$: Observable<any>;
  checkedAnalyzes$: Observable<any>;
  checkedGoods$: Observable<any>;

  constructor(
    private servicesService: ServicesService,
    private pagination: PaginationService,
    private analyzesService: AnalyzesService,
    private goodsService: GoodsService,
    private _tools: ToolsService
    ) { }

  ngOnInit() {
    this.reloadServices(this.servicesFilter);
    this.reloadGoods();
    this.initProfiles();
    this.initAnalyzes();

    this.form.get('discount').valueChanges.subscribe( (discount) => {
      this.payment.discount = new Discount(discount);
    });

  }

  ngOnDestroy() {
    this.clearCheckedGlobal();
    this.servicesSubscription.unsubscribe();
    this.analyzesSubscription.unsubscribe();
    this.goodsSubscription.unsubscribe();
  }

  reloadServices(params?: any) {
    this.services$ = this.servicesService.get(params)
      .map(result => result.items.map(item => new Service(item)));

    this.servicesSubscription = this.services$.subscribe();
  }

  initAnalyzes() {
    this.pagination.setParams({
      nPerPage: 10
    }, 'analyzes-check');
    this.reloadAnalyzes();
  }

  reloadAnalyzes(params?: any) {
    params = params !== undefined ? params : this.pagination.getParams('analyzes-check');

    this.analyzes$ = this.analyzesService.get(params)
      .map(analyzes => analyzes.map(analyz => new Analyzes(analyz)));
    const analyzesCount$ = this.analyzesService.getCount(params);

    const $combined = Observable.concat(
      this.analyzes$,
      analyzesCount$
    );

    this.analyzesSubscription = $combined
      .subscribe(
        result => {
          if(typeof result === 'number') {
            this.pagination.setParams({
              countItems: result
            }, 'analyzes-check');
            this.pagination.getPager('analyzes-check');
          }
        }
      );
  }

  reloadGoods(params?: any) {
    this.goods$ = this.goodsService.get(params);

    this.goodsSubscription = this.goods$.subscribe();
  }

  checkSelectedServices(service?: Service) {
    this.clearChecked();

    this.payment.services = this.servicesList.getSelectedItems().map( service => new Service(service) );
    this.payment.analyzes = this.analyzesList.getSelectedItems().map( analyz => new Analyzes(analyz) );
    this.payment.goods = this.goodsList.getSelectedItems().map( good => new Good(good) );
    
    this.reloadListChecked();
  }

  setSelectedItems() {
    this.servicesList.selectedItems = this.payment.services;
    this.analyzesList.selectedItems = this.payment.analyzes;
    this.goodsList.selectedItems = this.payment.goods;
  }

  unSelectAnalyzes(item: Analyzes) {
    this.analyzesList.unSelectItem(item);
    this.checkSelectedServices();
  }

  unSelectServices(item: Service) {
    this.servicesList.unSelectItem(item);
    this.checkSelectedServices();
  }

  unSelectGoods(item: Good) {
    this.goodsList.unSelectItem(item);
    this.checkSelectedServices();
  }

  clearChecked() {
    this.payment.services = [];
    this.payment.analyzes = [];
    this.payment.goods = [];
  }

  clearCheckedGlobal() {
    this.clearChecked();

    this.analyzesList.clearSelected();
    this.servicesList.clearSelected();
    this.goodsList.clearSelected();
  }

  changePageAnalyzes(params) {
    this.reloadAnalyzes(params);
  }

  onSearchAnalyzes(text: string) {
    let params = this.pagination.getParams('analyzes-check');
    params.currentPage = 1;
    params.search = text;

    this.reloadAnalyzes(params);
  }

  onSearchServices(text: string) {
    this.servicesFilter.search = text;

    this.reloadServices(this.servicesFilter);
  }

  onSearchGoods(text: string) {
    this.reloadGoods({search: text});
  }

  reloadListChecked() {
    this.checkedServices$ = this.getListCheckedServices();
    this.checkedGoods$ = this.getListCheckedGoods();
    this.checkedAnalyzes$ = this.getListCheckedAnalyzes();
  }

  getListCheckedServices() {
    return new Observable( observer => {
      let list = [];
      let added = {};

      this.payment.services.forEach((service) => {
        if(!!added[service._id]) {
          list.find((found, index) => {
            if(found.item._id == service._id) {
              list[index].count = list[index].count + 1;
              return true;
            }
          });
        }
        else {
          added[service._id] = true;
          list.push({count: 1, item: service});
        }
      });

      observer.next(list);
    });
  }

  getListCheckedAnalyzes() {
    return new Observable( observer => {
      let list = [];
      let added = {};

      this.payment.analyzes.forEach((analyz) => {
        if(!!added[analyz._id]) {
          list.find((found, index) => {
            if(found.item._id == analyz._id) {
              list[index].count = list[index].count + 1;
              return true;
            }
          });
        }
        else {
          added[analyz._id] = true;
          list.push({count: 1, item: analyz});
        }
      });

      observer.next(list);
    });
  }

  getListCheckedGoods() {
    return new Observable( observer => {
      let list = [];
      let added = {};

      this.payment.goods.forEach((good) => {
        if(!!added[good._id]) {
          list.find((found, index) => {
            if(found.item._id == good._id) {
              list[index].count = list[index].count + 1;
              return true;
            }
          });
        }
        else {
          added[good._id] = true;
          list.push({count: 1, item: good});
        }
      });

      observer.next(list);
    });
  }

  getCountServiceById(id) {
    let count = 0;
    this.payment.services.forEach((service) => {
      if(id == service._id) {
        count = count + 1;
      }
    });

    return count;
  }

  getCheckedServices() {
    return this.payment.services;
  }

  getCheckedAnalyzes() {
    return this.payment.analyzes;
  }

  getCheckedGoods() {
    return this.payment.goods;
  }

  setServicesFilterBySpecialists(specialists) {
    this.servicesFilter.tags = [];
    this.servicesFilter.variant = 'default';
    
    if(specialists && specialists.length > 0) {
      this.servicesFilter.tags = [];
      specialists.forEach((spec) => {
        this.servicesFilter.tags = this.servicesFilter.tags.concat(spec.tags);
      });
      
      this.servicesFilter.variant = (specialists.length > 1) ? 'default' : specialists[0].doctorate;
    }
  }

  initProfiles() {
    this.profiles = [
      { name: "Госп.", search: "02-029, 06-015, 07-049, 07-100, 07-025, 07-101, 03-003, 03-015, 03-010, 02-006"},
      { name: "Гинек.", search: "02-003, 12-001, 09-002, 09-025, 09-026, 09-095, 09-007, 10-001, 02-004" },
      { name: "Гельм.", search: "41701, 41901, 41902, 41903, 41904, 41906, 41909" },
      { name: "TORCH", search: "41201, 41202, 40703, 40704, 40801, 40802, 40901, 40902, 41601, 41602" },
      { name: "Горм.", search: "60304, 60303, 60305, 60702"},
      { name: "Горм.пол.", search: "08-016, 08-110, 08-117, 08-024, 08-050, 08-014, 08-126, 08-111, 08-119, 08-120, 08-122, 08-112, 08-003, 08-093, 08-089, 08-020, 08-021" },
      { name: "Ревм.", search: "90006, 90041, 90059, 90040" },
      { name: "Коаг.", search: "100001, 100003, 100002, 100004, 100005" },
      { name: "Биох.", search: "90001, 90004, 90005, 90006, 90007, 90008, 90009, 90014, 90015, 90016, 90017, 90048, 90037" },
      { name: "Холест.", search: "90009, 90010, 90011, 90012" },
      { name: "Онко.", search: "08-042, 08-005, 08-006, 08-004, 08-105, 08-007, 08-008, 08-057, 08-137, 08-038, 41-007, 08-135, 08-018" }
    ];
  }

  checkProfile(item) {
    this.onSearchAnalyzes(item.search);
  }

  resetAnalyzes() {
    this.pagination.setParams({currentPage: 1, text: ""}, 'analyzes-check');
    this.reloadAnalyzes();
  }

}



