import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';

import { Payment } from '../payment';
import { PaymentsService } from '../payments.service';
import { PaymentFormComponent } from '../payment-form/payment-form.component';

import { SpecialistsService } from '../../+specialists/specialists.service';
import { Specialist } from '../../+specialists/specialist';

import { ToolsService } from '../../shared/tools.service';

import { Good } from '../../+goods/good';
import { Service } from '../../+services/service';
import { Analyzes } from '../../+analyzes/analyzes';


@Component({
  selector: 'app-payment-view',
  templateUrl: './payment-view.component.html',
  styleUrls: ['./payment-view.component.css']
})
export class PaymentViewComponent implements OnInit {
  payment: Payment;

  specialists$:Observable<Specialist[]>;
  specialists: Specialist[];
  checkedServices$: Observable<Service[]>;
  checkedAnalyzes$: Observable<Analyzes[]>;
  checkedGoods$: Observable<Good[]>;
  checkedSpecialists: Specialist[];

  constructor(
    private specialistsService: SpecialistsService,
    private paymentService: PaymentsService,
    private tools: ToolsService,
    private _route: ActivatedRoute,
    private _router: Router
  ) { }

  ngOnInit() {
    this.specialists$ = this.loadSpecialists();

    this._route.params.forEach((item: Params) => {
      if(item['id']) {
        Observable.concat(this.specialists$, this.loadPayment(item['id']))
          .subscribe((item) => {
            if(item instanceof Object) {
              this.payment = new Payment(item);
              
              this.checkedSpecialists = this.payment.specialists.map( id => this.getSpecialist(id));
              this.checkedServices$ = this.getListCheckedServices();
              this.checkedGoods$ = this.getListCheckedGoods();
              this.checkedAnalyzes$ = this.getListCheckedAnalyzes();
            }
            if(item instanceof Array) {
              this.specialists = item;
            }
          });
        }
    });
  }

  getSpecialist(id) : Specialist {
    return this.specialists.find( spec => {
      if(spec._id == id) return true;
    });
  }

  loadPayment(_id: string) {
    return this.paymentService.getOne(_id);
  }

  loadSpecialists() {
    return this.specialistsService.get();
  }

  onPrint() {
    window.print();
  }

  getListCheckedServices() {
    return new Observable( observer => {
      var list = [];
      var added = {};

      this.payment.services.forEach((service) => {
        if(!!added[service._id]) {
          list.find((found, index) => {
            if(found.item._id == service._id) {
              list[index].count = list[index].count + 1;
              return true;
            }
          });
        }
        else {
          added[service._id] = true;
          list.push({count: 1, item: service});
        }
      });

      observer.next(list);
    });
  }

  getListCheckedAnalyzes() {
    return new Observable( observer => {
      var list = [];
      var added = {};

      this.payment.analyzes.forEach((analyz) => {
        if(!!added[analyz._id]) {
          list.find((found, index) => {
            if(found.item._id == analyz._id) {
              list[index].count = list[index].count + 1;
              return true;
            }
          });
        }
        else {
          added[analyz._id] = true;
          list.push({count: 1, item: analyz});
        }
      });

      observer.next(list);
    });
  }

  getListCheckedGoods() {
    return new Observable( observer => {
      var list = [];
      var added = {};

      this.payment.goods.forEach((good) => {
        if(!!added[good._id]) {
          list.find((found, index) => {
            if(found.item._id == good._id) {
              list[index].count = list[index].count + 1;
              return true;
            }
          });
        }
        else {
          added[good._id] = true;
          list.push({count: 1, item: good});
        }
      });

      observer.next(list);
    });
  }

}



