import { Component, OnInit, Input, Output, EventEmitter} from '@angular/core';

import { Payment } from '../payment';

@Component({
  selector: 'app-payments-list',
  templateUrl: './payments-list.component.html',
  styleUrls: ['./payments-list.component.css']
})
export class PaymentsListComponent implements OnInit {
  @Input('payments') 
    payments : Payment[] = []; 
  @Input('options') 
    options : { tools: { edit: boolean, collect: boolean, delete: boolean, pay: boolean }} = {
      tools: {
        edit: true,
        collect: true,
        delete: true,
        pay: true
      }
    }; 

  @Output('pay-click')
    ePayClick: EventEmitter<string> = new EventEmitter<string>();
  @Output('edit')
    eEdit: EventEmitter<string> = new EventEmitter<string>();
  @Output('collect')
    eCollect: EventEmitter<string> = new EventEmitter<string>();
  @Output('delete')
    eDelete: EventEmitter<string> = new EventEmitter<string>();
  @Output('show')
    eShow: EventEmitter<Payment> = new EventEmitter<Payment>();

  selectedItems: Payment[] = [];
  currentItem: Payment;

  //_payments: Payment[] = [];

  constructor() {}

  ngOnInit() {
  }

  selectItem(item): void {
    this.selectedItems.push(item);
  }

  unSelectItem(item) {
    this.selectedItems.forEach((found, index) => {
      if(found._id == item._id) this.selectedItems.splice(index, 1);
    });
  }

  isSelect(item: Payment) {
    return !!this.selectedItems.find((found) => {
      if(found._id === item._id) return true;
    });
  }

  getSelectedItems() {
    return this.selectedItems;
  }

  clearSelected() {
    this.selectedItems = []; 
  }

  payItem(item) {
    this.ePayClick.emit(item._id);
  }

  editItem(item) {
    this.eEdit.emit(item._id);
  }

  deleteItem(item) {
    this.currentItem = item;
    this.eDelete.emit(item);
  }

  showItem(item) {
    this.currentItem = item;
    this.eShow.emit(item);
  }

  collectItem(item) {
    this.currentItem = item;
    this.eCollect.emit(item._id);
  }

  clickItem(item) {
    console.log(item);
  }

}



