import { Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Specialist } from '../../../../+specialists/specialist';

@Component({
  selector: 'wd-day-report-col',
  templateUrl: './day-report-col.component.html',
  styleUrls: ['./day-report-col.component.css']
})
export class DayReportColComponent implements OnInit {
  @Input('report') set report(report: any){
    this.getSpecialistInReport(report);
  };

  @Input('totalPayments') totalPayments: Number = 0;
  @Input('totalServices') totalServices: Number = 0;
  @Input('totalAnalyzes') totalAnalyzes: Number = 0;
  @Input('totalGoods') totalGoods: Number = 0;

  constructor() {}

  ngOnInit() {
  } 

  getSpecialistInReport(report) {
    if(report) {
      this.totalPayments = report.totalPaymentsPrice;
      this.totalServices = report.servicesInfo ? (report.servicesInfo.totalServicesPrice - report.servicesDiscount) : 0;
      this.totalAnalyzes = report.analyzesInfo ? (report.analyzesInfo.totalAnalyzesPrice - report.analyzesDiscount) : 0;
      this.totalGoods = report.goodsInfo ? (report.goodsInfo.totalGoodsPrice - report.goodsDiscount) : 0;
    }
  } 
}


