import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import 'rxjs/Rx';
import { Observable } from 'rxjs/Rx';
import * as io from 'socket.io-client';
import { HttpClient } from '../+users/_services/http-client.service';
import { AuthenticationService } from '../+users/_services/authentication.service';

import { Payment, IPayment } from './payment';

@Injectable()
export class PaymentsService {
  private _baseUrl: string = "/api/v1/payments";
	private sokectUrl: string = '/payments';
	private socket;

  constructor(
    private _http: HttpClient,
		private _auth: AuthenticationService
  ) {}

	get(params?: any) : Observable<Payment[]> {
		let query = this.createQuaryParams(params);

		return this._http.get(this._baseUrl, { search: query })
			.map( (res: Response) => {
				return res.json().map((item) => new Payment(item))
			 })
			.catch( (error: any) => Observable.throw(error.json().error || "Server error"))
			.share();
	}

	getOne(id: string) : Observable<Payment> {
		return this._http.get(`${this._baseUrl}/${id}`)
			.map((res: Response) => res.json())
			.catch((error: any) => Observable.throw(error.json().errors || 'Server error'));
	}

  add(body: IPayment) : Observable<Payment> {
		let bodyString = JSON.stringify(body);
		let headers = new Headers({'Content-type': 'application/json'});
		let options = new RequestOptions({headers: headers});

		return this._http.post(this._baseUrl, bodyString, options)
			.map((res: Response) => res.json())
			.catch((error: any) => Observable.throw(error.json().errors || 'Server error'));
	}

	update(body: IPayment) : Observable<Payment> {
		let bodyString = JSON.stringify(body);
		let headers = new Headers({'Content-type': 'application/json'});
		let options = new RequestOptions({headers: headers});

		return this._http.put(`${this._baseUrl}/${body._id}`, JSON.stringify(body), options)
			.map((res: Response) => res.json())
			.catch((error: any) => Observable.throw(error.json().error || 'Server error'));
	}

	collect(body: IPayment) : Observable<Payment> {
		let bodyString = JSON.stringify(body);
		let headers = new Headers({'Content-type': 'application/json'});
		let options = new RequestOptions({headers: headers});

		return this._http.put(`${this._baseUrl}/collect/${body._id}`, JSON.stringify(body), options)
			.map((res: Response) => res.json())
			.catch((error: any) => Observable.throw(error.json().error || 'Server error'));
	}

	pay(body: IPayment) : Observable<Payment> {
		let bodyString = JSON.stringify(body);
		let headers = new Headers({'Content-type': 'application/json'});
		let options = new RequestOptions({headers: headers});

		return this._http.post(`${this._baseUrl}/pay/${body._id}`, JSON.stringify(body), options)
			.map((res: Response) => res.json())
			.catch((error: any) => Observable.throw(error.json().error || 'Server error'));
	}

	delete(id: string) : Observable<any> {
		return this._http.delete(`${this._baseUrl}/${id}`)
			.map((res: Response) => res.json())
			.catch((error: any) => Observable.throw(error.json().error || 'Server error'));
	}

	sendMessage(message){
    this.socket.emit('reserve-payment', 'new payment');    
  }
  
  getChangePaymentsEvents() {
		this.socket = io(this.sokectUrl, {
    	query: 'token=' + this._auth.cleanToken
		});
		
    let observable = new Observable(observer => {
      this.socket.on('change', (data) => {
        observer.next(data);    
      });
      return () => {
        this.socket.disconnect();
      };  
    })     
    return observable;
  }

	private createQuaryParams(params) : URLSearchParams {
		let query = new URLSearchParams();

		if(params) {
			if(params.nPerPage) query.set('nPerPage', params.nPerPage);
			if(params.currentPage) query.set('pageNumber', params.currentPage);
			if(params.specialists) query.set('specialists', params.specialists);
			if(params.date_from) query.set('date_from', params.date_from);
			if(params.date_to) query.set('date_to', params.date_to);
		}

		return query;
	}

	leaveSocket() {
		this.socket.disconnect();
	}
}



